.logo {
  width: 191px;
  height: 38px;
  @media (min-width: $tablet-width) {
    width: 174px;
    height: 52px;
  }
  @media (min-width: $desktop-width) {
    width: 202px;
    height: 60px;
  }
  &:hover {
    opacity: 0.8;
  }
  &:active {
    opacity: 0.6;
  }
}

.logo__img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: contain;
}
